<template>
	<fieldset class="form--block">
		<legend class="sub-header">Период без ДТП</legend>
		<div class="form--row">
			<input @change="changeDtpValue($event.target.value)"
				   class="form--radio"
				   ref= "les-one-year"
				   id="les-one-year"
				   type="radio"
				   name="dtp-period"
				   value="0">
			<label class="form--label " for="les-one-year">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 510 510">
						<use xlink:href="/Content/img/icons.svg#less-one"></use>
					</svg>
					<p class="label--text">менее года</p>
				</div>
			</label>

			<input @change="changeDtpValue($event.target.value)"
				   class="form--radio"
				   ref= "more-one-year"
				   id="more-one-year"
				   type="radio"
				   name="dtp-period"
				   :value="1">
			<label class="form--label " for="more-one-year">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 510 510">
						<use xlink:href="/Content/img/icons.svg#one"></use>
					</svg>
					<p class="label--text">более года</p>
				</div>
			</label>

			<input @change="changeDtpValue($event.target.value)"
				   class="form--radio"
				   ref= "more-two-years"
				   id="more-two-years"
				   type="radio"
				   name="dtp-period"
				   :value="2">
			<label class="form--label " for="more-two-years">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 510 510">
						<use xlink:href="/Content/img/icons.svg#two"></use>
					</svg>
					<p class="label--text">более 2-х лет</p>
				</div>
			</label>

			<input @change="changeDtpValue($event.target.value)"
				   class="form--radio"
				   ref= "more-three-years"
				   id="more-three-years"
				   type="radio"
				   name="dtp-period"
				   :value="3">
			<label class="form--label " for="more-three-years">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 510 510">
						<use xlink:href="/Content/img/icons.svg#three"></use>
					</svg>
					<p class="label--text">более 3-х лет</p>
				</div>
			</label>

			<input @change="changeDtpValue($event.target.value)"
				   class="form--radio"
				   ref= "more-four-years"
				   id="more-four-years"
				   type="radio"
				   name="dtp-period"
				   :value="4">
			<label class="form--label " for="more-four-years">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 510 510">
						<use xlink:href="/Content/img/icons.svg#four"></use>
					</svg>
					<p class="label--text">более 4-х лет</p>
				</div>
			</label>
		</div>
	</fieldset>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
			}
		},
		computed: {
			...mapGetters("dtp",{
				dtp: "dtp"
			}),
			...mapGetters("user",{
        isLegalEntity: "isLegalEntity"
			})
		},
		methods: {
			...mapActions("dtp", {
				changeDtpValue: "changeDtpValue"
			}),
			setChecked() {
				for (let key in this.$refs) {
					if(this.$refs[key].value === this.dtp.toString()) {
						this.$refs[key].checked = true;
					}
				}
			}
		},
		mounted () {
			this.setChecked();
		},
		destroyed() {
			this.changeDtpValue(false);
		}
	}
</script>



