<template>
	<fieldset v-show="!hidePrivileges" class="form--block">
		<legend class="sub-header">Права на пільги</legend>

		<fieldset v-if="showDetails" class="form--block">
			<p class="form--text">
			Відповідно до Закону <i>«Про обов'язкове страхування цивільно-правової відповідальності власників наземних транспортних засобів»</i>, пільги <b class="form--text__bold">не передбачені</b> для ТЗ с объемом двигателя более 2500 куб.см.
			</p>

			<legend class="sub2-header">Уточните объем двигателя</legend>
			<div class="form--row">
				<input v-model="detailVolume"
					   value="больше 2500 куб.см"
					   class="form--radio"
					   id="more-2500"
					   type="radio"
					   name="detail-volume" >
				<label class="form--label form--label__more-2500" for="more-2500"> больше 2500 куб.см.</label>
				<input v-model="detailVolume"
					   value="меньше 2500 куб.см"
					   class="form--radio"
					   id="less-2500"
					   type="radio"
					   name="detail-volume" >
				<label class="form--label form--label__less-2500" for="less-2500"> меньше 2500 куб.см.</label>
			</div>
		</fieldset>

		<div v-show="!hidePrivilegesBtns">

			<fieldset>
				<legend v-show="showDetails" class="sub2-header">Льготы</legend>
				<div class="form--row">
					<input @change="changePrivilegesValue($event.target.value)"
						   class="form--radio"
						   ref="privilege-no"
						   id="privilege-no"
						   type="radio"
						   name="privilege"
						   :value="false">
					<label class="form--label form--label__privileges" for="privilege-no">
						<div class="form--icon-wrap ">
							<svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 129 129">
						   		<use xlink:href="/Content/img/icons.svg#no-privilege"></use>
						   	</svg>
					   		<p class="label--text">Нету</p>
				   		</div>
					</label>

					<input @change="changePrivilegesValue($event.target.value)"
						   class="form--radio"
						   ref="privilege-pensioner"
						   id="privilege-pensioner"
						   type="radio"
						   name="privilege"
						   value="Пенсионер">
				   <label class="form--label form--label__privileges" for="privilege-pensioner">
				   		<div class="form--icon-wrap ">
						   	<svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 512.001 512.001">
						   		<use xlink:href="/Content/img/icons.svg#old"></use>
						   	</svg>
					   		<p class="label--text">Пенсионер</p>
				   		</div>
				   </label>

					<input @change="changePrivilegesValue($event.target.value)"
						   class="form--radio"
						   ref="privilege-veteran"
						   id="privilege-veteran"
						   type="radio"
						   name="privilege"
						   value="Ветеран">
					<label class="form--label form--label__privileges" for="privilege-veteran">
						<div class="form--icon-wrap ">
							<svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 496 496">
						   		<use xlink:href="/Content/img/icons.svg#veteran"></use>
						   	</svg>
					   		<p class="label--text">Ветеран</p>
					   	</div>
					</label>

					<input @change="changePrivilegesValue($event.target.value)"
						   class="form--radio"
						   ref="privilege-invalid"
						   id="privilege-invalid"
						   type="radio"
						   name="privilege"
						   value="Инвалид второй группы">
					<label class="form--label form--label__privileges" for="privilege-invalid">
						<div class="form--icon-wrap ">
							<svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 496 496">
						   		<use xlink:href="/Content/img/icons.svg#disabled"></use>
						   	</svg>
					   		<p class="label--text">Инвалид второй группы</p>
					   	</div>
					</label>

					<input @change="changePrivilegesValue($event.target.value)"
						   class="form--radio"
						   ref="privilege-chernobyl"
						   id="privilege-chernobyl"
						   type="radio"
						   name="privilege"
						   value="Чернобылец первой группы">
					<label class="form--label form--label__privileges" for="privilege-chernobyl">
						<div class="form--icon-wrap ">
							<svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 461.806 461.806">
						   		<use xlink:href="/Content/img/icons.svg#radiation"></use>
						   	</svg>
					   		<p class="label--text">Чернобылец первой группы</p>
				   		</div>
					</label>
				</div>
			</fieldset>

			<div v-if="showPrivilegesText" class="form--text">
				<h5 class="form--header__privileges">Правa на льготы имеют лица, у которых:</h5>
				<ul class="form--list">
					<li>объем двигателя ТЗ до 2500 см3</li>
					<li>ТЗ принадлежит этому гражданину на правах собственности</li>
					<li>управление ТЗ осуществляется <b class="form--text__bold">только</b> этим гражданином, который имеет право на льготы</li>
					<li>на ТЗ не производится предоставление платных услуг по перевозке пассажиров или груза</li>
				</ul>
				<a href="https://zakon.rada.gov.ua/laws/show/1961-15" class="form--link form--link__privileges">Полный закон</a>
			</div>

			<div v-if="showPrivilegesText" class="form--text">
				<h5 class="form--header__privileges">Ответственность за нарушение условий страхования</h5>
				<p>В случае нарушения вышеупомянутых условий, виновник ДТП <b class="form--text__bold">должен компенсировать 50% от суммы страхового возмещения</b> страховой компании</p>
			</div>
		</div>
	</fieldset>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
				detailVolume: "меньше 2500 куб.см"
			}
		},
		computed: {
			...mapGetters("privileges",{
				privileges: "privileges"
			}),
			...mapGetters("user",{
        isLegalEntity: "isLegalEntity"
			}),
			...mapGetters("transport",{
				transType: "transType",
        transExtraName: "transExtraName"
			}),
			hidePrivileges() {
				return this.isLegalEntity === true ||
					   (this.transType === "Легковий автомобіль" && this.transExtraName === "B 4") ||
					   this.transType === "Автобус" ||
					   this.transType === "Грузовий атомобіль" ||
					   this.transType === "Причіп"

			},
			showDetails() {
				return this.transType === "Легковий автомобіль" && this.transExtraName === "B 3"
			},
			hidePrivilegesBtns() {
				return this.detailVolume === "больше 2500 куб.см"
			},
			showPrivilegesText() {
				return this.privileges
			}
		},
		methods: {
			...mapActions("privileges", {
				changePrivilegesValue: "changePrivilegesValue"
			}),
			resetPrivileges() {
				this.changePrivilegesValue(false);
			},
			setChecked() {
				for (let key in this.$refs) {
					if(this.$refs[key].value === this.privileges.toString()) {
						this.$refs[key].checked = true;
					}
				}
			}
		},
		mounted() {
			this.setChecked();
		},
		updated() {
			if (this.hidePrivileges || this.hidePrivilegesBtns) {
				this.resetPrivileges();
				this.setChecked();
			}
		}
	}
</script>

<style lang="less">
.form {
	&--header {
		&__privileges {
			font-size: 25px;
			margin-bottom: 5px;
			@media only screen and (max-width:576px) {
				line-height: 1;
			}
		}
	}
	&--link {
		&__privileges {
			display: block;
		}
	}
	&--label {
		&__privileges {
			@media only screen and (max-width:1195px) {
				height: 122px;
			}
			@media only screen and (max-width:320px) {
				height: 132px;
			}
		}
	}
}
</style>



