<template>
	<article class="article">
		<h2 class="form--header header header__no-underline">Додаткова інформація</h2>
		<dtp></dtp>
		<privileges></privileges>
		<osago-limits></osago-limits>
	</article>
</template>

<script>
	import Dtp from "./ExtraInfoComps/Dtp.vue";
	import Privileges from "./ExtraInfoComps/Privileges.vue";
	import OsagoLimits from "./ExtraInfoComps/OsagoLimits.vue";
	export default {
		data() {
			return {

			}
		},

		components: {
			Dtp,
			Privileges,
			OsagoLimits
		},

		mounted() {
			const biggerNeighbour = document.querySelector("label[for='notTaxi']");
			const biggerNeighbourHeight = biggerNeighbour.getBoundingClientRect().height;
			const lowerNeighbour = document.querySelector("label[for='entity']");

			lowerNeighbour.style.height = biggerNeighbourHeight + "px";
		}
	}
</script>



