<template>
	<fieldset class="form--block user">
		<legend class="sub-header">Власник ТЗ</legend>
		<div class="form--row">
			<input @change="changeUserType($event.target.value)"
				   class="form--radio"
				   id="phys"
				   type="radio"
				   name="user"
				   ref="user1"
				   :value="false">
				   <label class="form--label " for="phys">
					   	<div class="form--icon-wrap">
					   		<svg class="form--icon form--icon__small" aria-hidden="true" viewBox="0 0 482.9 482.9">
					   			<use xlink:href="/Content/img/icons.svg#phys"></use>
					   		</svg>
					   		<p class="label--text">Фізична особа</p>
					   	</div>
				   </label>

			<input @change="changeUserType($event.target.value)"
				   class="form--radio"
				   id="entity"
				   type="radio"
				   ref="user2"
				   name="user"
				   :value="true">
			<label class="form--label " for="entity">
				<div class="form--icon-wrap">
				   		<svg class="form--icon form--icon__small" aria-hidden="true" viewBox="0 0 480 480">
				   			<use xlink:href="/Content/img/icons.svg#entity"></use>
				   		</svg>
				   		<p class="label--text">Юридична особа</p>
				   	</div>
			</label>
		</div>
	</fieldset>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		computed: {
			...mapGetters("user",{
        isLegalEntity: "isLegalEntity"
			})
		},
		methods: {
			...mapActions("user", {
				changeUserType: "changeUserType"
			})
		},
		mounted () {
			for (let key in this.$refs) {
				if(this.$refs[key].value === this.isLegalEntity.toString()) {
				this.$refs[key].checked = true;
				}
			}
		}
	}
</script>

<style lang="less">
	.user {
		max-width: 650px;
		width: 50%;
		@media only screen and (max-width:880px) {
			width: 100%
		}
	}
</style>



