<template>
	<fieldset class="form--block taxi" v-show="!hideTaxi">
		<legend class="sub-header">ТЗ використовується як таксі?</legend>
		<div class="form--row">
			<input :checked="!taxi"
				   @change="changeTaxiValue(false)"
				   class="form--radio"
				   id="notTaxi"
				   type="radio"
				   name="taxi">
			<label class="form--label " for="notTaxi">
				<div class="form--icon-wrap taxi--icon-wrap">
					<svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 510 510">
						<use xlink:href="/Content/img/icons.svg#not-taxi"></use>
					</svg>
					<p class="label--text">Ні, не використовується</p>
				</div>
			</label>

			<input :checked="taxi"
				   @change="changeTaxiValue(true)"
				   class="form--radio"
				   id="taxi"
				   type="radio"
				   name="taxi">
			<label class="form--label " for="taxi">
				<div class="form--icon-wrap taxi--icon-wrap">
					<svg class="form--icon form--icon__small" aria-hidden="true" viewBox="0 0 226.969 226.969">
						<use xlink:href="/Content/img/icons.svg#taxi"></use>
					</svg>
					<p class="label--text">Так, використовується</p>
				</div>
			</label>
		</div>
	</fieldset>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
			}
		},
		computed: {
			...mapGetters("taxi",{
				taxi: "taxi"
			}),
			...mapGetters("transport",{
				transType: "transType",
        transExtraName: "transExtraName"
			}),
			hideTaxi() {
				return this.transType === "Мотоцикл" ||
					   (this.transType === "Автобус" && this.transExtraName === "D 2") ||
					   this.transType === "Грузовий атомобіль" ||
					   this.transType === "Причіп"
			}
		},
		methods: {
			...mapActions("taxi", {
				changeTaxiValue: "changeTaxiValue"
			})
		},
		updated() {
			if (this.hideTaxi) {
				this.changeTaxiValue(false);
			}
		}
	}
</script>

<style lang="less">
	.taxi {
		max-width: 650px;
		width: 50%;
		@media only screen and (max-width:880px) {
			width: 100%
		}
	}
</style>



