<template>
  <div>
    <h1 class="header">Калькулятор ОСЦПВ</h1>
    <div class="form--wrap">
      <p>
        Наш сервис предоставляет возможность быстро и удобно узнать стоимость страховки ОСАГО для Вашего транспортного средства в разных страховых компаниях. Заполнение простой и понятной формы займет не более минуты Вашего времени. Вам следует лишь указать данные о своем транспортном средстве, город проживания и водительский стаж. После этого Вы увидите таблицу, в которой указана цена на страховку, а также бонусы различных страховых компаний (к примеру, карта техпомощи, вызов эвакуатора, выезд аварийного комиссара, юридическая поддержка на месте ДТП и т.д.). </p>
      <p>
        Следует учесть, что стоимость страхования по ОСАГО (Автоцивилка) меняется почти каждый год – в нашей стране ее определяет государство. Действия полиса распространяется только на территорию Украины.</p>
    </div>

    <form>
      <transport></transport>
      <reg-and-exp></reg-and-exp>
      <extra-info></extra-info>

      <div class="form--submit">
        <input @click.prevent="$emit('submit')"
               :disabled="errorsLength > 0"
               class="form--send"
               type="submit"
               value="Рассчитать">
        <span class="form--error" v-if="errorsLength > 0">Дані заповнені некорректно</span>
      </div>
    </form>
  </div>
</template>

<script>
  import Transport from "./CalcForm/Transport";
  import RegAndExp from "./CalcForm/RegAndExp";
  import ExtraInfo from "./CalcForm/ExtraInfo";
  import {mapGetters} from "vuex";

  export default {
    props: ["showCompaniesList"],

    computed: {
      ...mapGetters("errors", {
        "errorsLength": "errorsLength"
      }),
      ...mapGetters([
        "transportData"
      ])
    },

    components: {
      Transport,
      RegAndExp,
      ExtraInfo
    }
  }
</script>





