<template>
  <fieldset class="form--block place">
    <legend class="sub-header">Место регистрации ТЗ</legend>
    <div class="place--row">
      <label for="adress" class="place--label-cont form--label-cont">
        Адресс
        <input @change="clearPlaceData();changePlaceType($event.target.value)"
               :checked="placeType === 'address'"
               id="adress"
               type="radio"
               name="document"
               value="address"
               class="form--check form--radio"
               required>
        <span class="form--checkmark place--checkmark"></span>
      </label>

      <label for="code" class="place--label-cont form--label-cont">
        Код МРЕО
        <input @change="clearPlaceData();changePlaceType($event.target.value); getCodes()"
               :checked="placeType === 'code'"
               id="code"
               type="radio"
               name="document"
               value="code"
               class="form--check form--radio"
               required>
        <span class="form--checkmark place--checkmark"></span>
      </label>
    </div>

    <div class="place--row">
      <div v-if="placeType === 'address'" class="form--sub-block place--block">
        <template v-if="!foreignBool">
        <div v-if="placeType === 'address'" class="place--left-row">
          <h3 class="sub2-header place--sub2-header">Выберете адрес</h3>
          <label class="place--label" for="region">Область</label>
          <v-select :options="regions"
                    @input="changeRegion($event); requestToServ(`../Conditions/GetCities?regionName=${activeRegion}`, callbackToGetCities)"
                    :value="activeRegion"
                    :disabled="foreignBool"
                    inputId="region"
                    placeholder="Ваша область"
                    :class="errors.region ? 'form--input__error' : false"
                    class="form--input">

            <span slot="no-options">Не найдено совпадений</span>
          </v-select>
          <span class="form--error" v-if="errors.region">Укажите область</span>
        </div>

        <div class="place--left-row">
          <label class="place--label" for="city">Город</label>
          <v-select :options="cities"
                    @input="changeCity($event)"
                    :value="activeCity"
                    :disabled="foreignBool"
                    inputId="city"
                    placeholder="Ваш город"
                    :class="errors.city ? 'form--input__error' : false"
                    class="form--input">

            <span slot="no-options">Не найдено совпадений</span>
          </v-select>
          <span class="form--error" v-if="errors.city">Укажите город</span>
        </div>
        </template>

        <div class="place--left-row place--left-row__foreign">
          <label class="form--label-cont place--foreign" for="place-other">
            Не Украина
            <input class="form--check form--radio"
                   @change="changeForeign(!foreignBool); getCountries()"
                   :checked="foreignBool"
                   id="place-other"
                   type="checkbox">
            <span class="form--checkmark"></span>
          </label>
          <div v-if="foreignBool">
            <label class="place--label" for="country">Страна</label>
            <v-select :options="countries"
                      @input="changeForeign($event)"
                      :value="foreign"
                      placeholder="Ваша страна"
                      inputId="country"
                      :class="errors.country ? 'form--input__error' : false"
                      class="form--input">

              <span slot="no-options">Не найдено совпадений</span>
            </v-select>
            <span class="form--error" v-if="errors.country">Укажите страну</span>
          </div>
        </div>
      </div>

      <div v-if="placeType === 'code'" class="form--sub-block place--block">
        <h3 class="sub2-header place--sub2-header place--sub2-header__center">Введите код МРЕО</h3>
        <v-select :options="codes"
                  label="codeNum"
                  @input="changeCode($event)"
                  :value="activeCode"
                  inputId="country"
                  placeholder="Код МРЕО"
                  :class="errors.code ? 'form--input__error' : false"
                  class="form--input place--input__center">

          <template slot="option" slot-scope="option">
            <span>{{option.codeNum}}</span>
            <span>{{option.regionName}}</span>
            <span>{{option.cityName}}</span>
          </template>

          <span slot="no-options">Не найдено совпадений</span>
        </v-select>
        <span class="form--error" v-if="errors.code">Укажите код МРЕО</span>
      </div>
    </div>
  </fieldset>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";
  import vSelect from "vue-select";

  export default {
    data() {
      return {
        cities: [],
      }
    },

    computed: {
      ...mapGetters("errors", {
        "errors": "errors"
      }),
      ...mapGetters("place", {
        regions: "regions",
        placeType: "placeType",
        activeRegion: "activeRegion",
        activeCity: "activeCity",
        codes: "codes",
        activeCode: "activeCode",
        foreign: "foreign",
        foreignBool: "foreignBool",
        countries: "countries"
      })
    },

    methods: {
      ...mapActions("place", {
        changeRegions: "changeRegions",
        changePlaceType: "changePlaceType",
        changeRegion: "changeRegion",
        changeCity: "changeCity",
        changeCodes: "changeCodes",
        changeCode: "changeCode",
        changeForeign: "changeForeign",
        changeCountries: "changeCountries"
      }),
      ...mapActions([
        "changeErrors"
      ]),

      getRegions() {
        if (!this.codes.length) {
          this.requestToServ("../Conditions/GetRegions", (answer) => {
            this.changeRegions(answer);
          });
        }
      },

      requestToServ(url, callback) {
        const xmlHttp = new XMLHttpRequest();
        xmlHttp.open("get", url, true);

        xmlHttp.send();

        xmlHttp.onreadystatechange = () => {
          console.log(xmlHttp);
          if (xmlHttp.readyState === 4) {
            const answer = JSON.parse(xmlHttp.responseText);
            callback(answer);
          }
        }
      },

      getCodes() {
        if (!this.codes.length) {
          this.requestToServ("../Conditions/GetCodesTSC", (answer) => {
            this.changeCodes(answer);
          });
        }
      },

      getCountries() {
        if (!this.countries.length) {
          this.requestToServ("../Conditions/GetCountries", (answer) => {
            this.changeCountries(answer);
          });
        }
      },

      callbackToGetCities(answer) {
        this.cities = answer;
      },

      clearPlaceData() {
        if (this.placeType === "address") {
          this.changeCode(null);
        }
        else if (this.placeType === "code") {
          this.changeRegion(null);
          this.changeCity(null);
          this.changeForeign(false);
        }
      }
    },

    created() {
      this.getRegions();
    },

    components: {
      vSelect
    }
  }
</script>

<style lang="less">
  .place {
    @media only screen and (max-width: 374px) {
      margin-bottom: 50px;
    }
    &--row {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
    }
    &--block {
      margin: 0 35px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      @media only screen and (max-width: 700px) {
        width: 100%;
      }
    }
    &--left-row {
      margin-bottom: 20px;
      &__foreign {
        @media only screen and (max-width: 700px) {
          margin-bottom: 0;
        }
      }
    }
    &--separetor {
      font-size: 40px;
      font-weight: bold;
      text-transform: uppercase;
      color: #0093e8;
      @media only screen and (max-width: 700px) {
        margin: 8px 0;
        font-size: 32px;
      }
    }
    &--sub2-header {
      @media only screen and (max-width: 700px) {
        margin-bottom: auto;
      }
      &__center {
        @media only screen and (max-width: 576px) {
          margin-bottom: 5px;
        }
      }
    }
    &--label {
      display: block;
      font-weight: bold;
      margin-top: 7px;
      margin-bottom: 7px;
      @media only screen and (max-width: 576px) {
        margin-bottom: 0;
        margin-top: 10px;
      }
    }

    &--label-cont {
      display: inline-block;
      margin-right: 20px;
      margin-bottom: 20px;
    }

    &--checkmark {
      border-radius: 50%;
      margin-left: 0;
      &::after {
        top: 9px;
        left: 9px;
        width: 7px;
        height: 7px;
        border-radius: 50%;
        border: none;
        background: white;
      }
    }

    &--input {
      &__center {
      }
    }
  }
</style>



