<template>
	<fieldset v-if="showCalendar">
		<legend class="sub2-header">Выберите дату окончания ОТК</legend>
		<input @input="countDays($event.target.value)"
			   :min="todayForCalendar"
			   class="form--input form--calendar"
			   type="date"
			   name="calendar" >
	</fieldset>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
				today: new Date(),
			}
		},
		computed: {
			...mapGetters("otk",{
				otk: "otk"
			}),
			showCalendar() {
				return this.otk
			},
			todayForCalendar() {
				return this.today.toISOString().split("T")[0]
			}
		},
		methods: {
			...mapActions("otk", {
				changeOtkTerm: "changeOtkTerm"
			}),
			countDays(e) {
				function parseDate(str) {
					const ymd = str.split("-");
					return new Date(ymd[0], ymd[1] - 1, ymd[2]);
				}
				function datediff(first, second) {
					return Math.round((second.setHours(0,0,0,0) - first.setHours(0,0,0,0))/(1000*60*60*24));
				}
				this.changeOtkTerm(datediff(this.today, parseDate(e)));
			}
		}
	}

</script>

<style>

</style>



