<template>
  <div>
    <fieldset class="form--block">
      <legend class="sub-header">Период страхования</legend>

      <otk v-if="showOtk"></otk>

      <template v-if="(otk ? otkTerm : true)">
        <label v-if="showOtk" for="period" class="sub2-header">Срок действия</label>
        <select :value="period"
                @change="changePeriod($event.target.value)"
                class="form--input"
                :class="periodError ? 'form--input__error' : false">
          <option :value="false"
                  disabled
                  style="display:none">
          </option>
          <option v-for="(option, key, index) in periods"
                  v-if="option.isAvailable"
                  id="period"
                  :value="option.name">
            {{option.title}}
          </option>
        </select>

        <span class="form--error" v-if="periodError">Выберете период страхования</span>
      </template>
    </fieldset>
  </div>
</template>

<script>
  import Otk from "./Otk/Otk.vue";
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";

  export default {
    data() {
      return {}
    },
    computed: {
      ...mapGetters("errors", {
        "errors": "errors"
      }),
      ...mapGetters("period", {
        period: "period"
      }),
      ...mapGetters("place", {
        foreign: "foreign"
      }),
      ...mapGetters("otk", {
        otk: "otk",
        otkTerm: "otkTerm"
      }),
      ...mapGetters("transport", {
        transType: "transType",
        transExtraName: "transExtraName"
      }),
      ...mapGetters("user", {
        isLegalEntity: "isLegalEntity"
      }),
      periods() {
        return [
          {
            title: "15 днів",
            name: 0.5,
            isAvailable: !this.transType === "Автобус" || this.foreign || this.otkTerm >= 15
          },
          {
            title: "1 місяць",
            name: 1.0,
            isAvailable: !this.transType === "Автобус" || this.foreign || this.otkTerm >= 30,
          },
          {
            title: "2 місяці",
            name: 2.0,
            isAvailable: !this.transType === "Автобус" || this.foreign || this.otkTerm >= 60,
          },
          {
            title: "3 місяці",
            name: 3.0,
            isAvailable: !this.transType === "Автобус" || this.foreign || this.otkTerm >= 90,
          },
          {
            title: "4 місяці",
            name: 4.0,
            isAvailable: !this.transType === "Автобус" || this.foreign || this.otkTerm >= 120,
          },
          {
            title: "5 місяців",
            name: 5.0,
            isAvailable: !this.transType === "Автобус" || this.foreign || this.otkTerm >= 150,
          },
          {
            title: "6 місяців",
            name: 6.0,
            isAvailable: !this.transType === "Автобус" || !this.otk || this.otkTerm >= 180,
          },
          {
            title: "7 місяців",
            name: 7.0,
            isAvailable: !this.transType === "Автобус" || !this.otk || this.otkTerm >= 210,
          },
          {
            title: "8 місяців",
            name: 8.0,
            isAvailable: !this.transType === "Автобус" || !this.otk || this.otkTerm >= 240,
          },
          {
            title: "9 місяців",
            name: 9.0,
            isAvailable: !this.transType === "Автобус" || !this.otk || this.otkTerm >= 270,
          },
          {
            title: "10 місяців",
            name: 10.0,
            isAvailable: !this.transType === "Автобус" || !this.otk || this.otkTerm >= 300,
          },
          {
            title: "11 місяців",
            name: 11.0,
            isAvailable: !this.transType === "Автобус" || !this.otk || this.otkTerm >= 330,
          },
          {
            title: "12 місяців",
            name: 12.0,
            isAvailable: !this.transType === "Автобус" || !this.otk || this.otkTerm >= 360,
          }
        ]
      },
      showOtk() {
        return (this.transType === "Легковий автомобіль" && this.isLegalEntity === true) ||
          this.transType === "Грузовий атомобіль" ||
          this.transType === "Автобус" ||
          (this.transType === "Причіп" && this.transExtraName === "E")
      },
      periodError() {
        if (this.otk) {
          if (this.errors.period && this.otkTerm !== null) {
            return true
          }
        }
        else {
          if (this.errors.period) {
            return true
          }
        }
      }
    },
    methods: {
      ...mapActions("period", {
        changePeriod: "changePeriod"
      })
    },
    components: {
      Otk
    },
    updated() {
      // Для создания ошибки, когда ни один из периодов невозможен
      let trues = 0;
      this.periods.forEach((period) => {
        if (period.isAvailable) {
          trues++;
        }
      });
      if (trues <= 0) {
        this.changePeriod(false);
      }
    }
  }
</script>



