<template>
	<div>
		<fieldset class="form--block" v-if="!hideDtp">
			<legend class="sub-header">Были ли ДТП по Вашей вине?</legend>
			<div class="form--row">
				<input @change="changeDtpValue(false)"
					   :checked="dtp === false"
					   class="form--radio"
					   id="no-dtp"
					   type="radio"
					   name="dtp"
					   :value="false">
				<label class="form--label" for="no-dtp">
					<div class="form--icon-wrap">
						<svg class="form--icon form--icon__small dtp--icon__small-not" aria-hidden="true" viewBox="0 0 510 510">
							<use xlink:href="/Content/img/icons.svg#not-dtp"></use>
						</svg>
						<p class="label--text">Нет, не было</p>
					</div>
				</label>
				<input @change="changeDtpValue(defaultDtpPeriod)"
					   :checked="dtp !== false"
					   class="form--radio"
					   id="yes-dtp"
					   type="radio"
					   name="dtp"
					   :value="true">
				<label class="form--label" for="yes-dtp">
					<div class="form--icon-wrap">
						<svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 510 510">
							<use xlink:href="/Content/img/icons.svg#dtp"></use>
						</svg>
						<p class="label--text">Да, были</p>
					</div>
				</label>
			</div>
		</fieldset>

		<dtp-period v-if="dtp !== false"></dtp-period>
	</div>
</template>

<script>
	import DtpPeriod from "./DtpPeriod/DtpPeriod.vue";
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
			}
		},
		computed: {
			...mapGetters("dtp",{
				dtp: "dtp"
			}),
			...mapGetters("user",{
        isLegalEntity: "isLegalEntity"
			}),
			hideDtp() {
				return this.isLegalEntity === true
			},
			defaultDtpPeriod() {
				return this.dtp || 0
			}
		},
		methods: {
			...mapActions("dtp", {
				changeDtpValue: "changeDtpValue"
			})
		},
		components: {
			DtpPeriod
		}
	}
</script>

<style lang="less">
	.dtp {
		&--icon {
			&__small-not {
				width: 37%;
			}
		}
	}
</style>



