<template>
  <article class="article transport">
    <h2 class="header form--header header__no-underline">Інформація про транспортний засіб</h2>
    <div class="transport--wrap">
      <fieldset class="form--block">
        <legend class="sub-header sub-header__active">Тип транспортного засобу</legend>
        <div class="form--row">
          <template v-for="(el, index, key) in trans">
            <input :id="'trans-type'+index"
                   type="radio"
                   :value="el.type"
                   @change="onTransTypeChange($event.target.value, index)"
                   :checked="index == selectedTransIndex"
                   name="trans-type"
                   class="form--radio">
            <label class="form--label form--label__transport" :class="'form--label__' + el.class"
                   :for="'trans-type'+index">
              <div class="label--wrap">
                <div class="form--icon-wrap">
                  <svg class="form--icon" :class="'form--icon__' + el.class" aria-hidden="true" :viewBox="el.viewBox">
                    <use v-bind="{'xlink:href':'Content/img/icons.svg#'+el.class}"></use>
                  </svg>
                  <p class="label--text">{{el.type}}</p>
                </div>
              </div>
            </label>
          </template>
        </div>
      </fieldset>

      <fieldset class="form--block">
        <legend class="sub-header">{{trans[selectedTransIndex].extra.header}}</legend>
        <div class="form--row">
          <div class="transport--el" v-for="(param, index, key) in trans[selectedTransIndex].extra.params">
            <input :id="'trans-extra'+index"
                   type="radio"
                   :key="index"
                   :value="param.name"
                   :checked="index == selectedExtraIndex"
                   @change="changeTransExtraName($event.target.value)"
                   name="trans-extra"
                   class="form--radio">

            <label class="form--label form--label__extra" :key="index + '0'"
                   :for="'trans-extra'+index">{{param.title}}</label>
          </div>
        </div>
      </fieldset>
    </div>
  </article>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";

  export default {
    data() {
      return {
        trans: [
          {
            type: "Легковий автомобіль",
            class: "passenger-car",
            viewBox: "0 0 512 237",
            extra: {
              header: "Об'єм двигуна",
              params: [
                {
                  title: "до 1600 см³",
                  name: "B 1"
                },
                {
                  title: "1601 - 2000 см³",
                  name: "B 2"
                },
                {
                  title: "2001 - 3000 см³",
                  name: "B 3"
                },
                {
                  title: "3001 см³ і більше",
                  name: "B 4"
                }
              ]
            }
          },
          {
            type: "Електромобіль",
            class: "electric-car",
            viewBox: "0 0 337 328",
            extra: {
              header: "Потужність",
              params: [
                {
                  title: "до 60,7 кВт",
                  name: "B 1"
                },
                {
                  title: "60,8 - 76 кВт",
                  name: "B 2"
                },
                {
                  title: "76 - 114 кВт",
                  name: "B 3"
                },
                {
                  title: "114 кВт и более",
                  name: "B 4"
                }
              ]
            }
          },
          {
            type: "Автобус",
            class: "bus",
            viewBox: "0 0 582 327",
            extra: {
              header: "Кількість місць",
              params: [
                {
                  title: "до 20 включно",
                  name: "D 1"
                },
                {
                  title: "21 і більше",
                  name: "D 2"
                }
              ]
            }
          },
          {
            type: "Грузовий атомобіль",
            class: "truck",
            viewBox: "0 0 612 434",
            extra: {
              header: "Маса автомобіля",
              params: [
                {
                  title: "до 2 тон включно",
                  name: "C 1"
                },
                {
                  title: "більше 2 тон",
                  name: "C 2"
                }
              ]
            }
          },
          {
            type: "Мотоцикл",
            class: "moto",
            viewBox: "0 0 337 240",
            extra: {
              header: "Об'єм двигуна",
              params: [
                {
                  title: "до 300 см³",
                  name: "A 1"
                },
                {
                  title: "більше 300 см³",
                  name: "A 2"
                }
              ]
            }
          },
          {
            type: "Причіп",
            class: "trailer",
            viewBox: "0 0 492 289",
            extra: {
              header: "Тип причепа",
              params: [
                {
                  title: "до лекового авто",
                  name: "E"
                },
                {
                  title: "до грузового авто",
                  name: "F"
                }
              ]
            }
          }
        ]
      }
    },
    computed: {
      ...mapGetters("transport", {
        transType: "transType",
        transExtraName: "transExtraName"
      }),
      selectedTransIndex() {
        for (let i = 0; i < this.trans.length; i++) {
          if (this.trans[i].type === this.transType) {
            return i
          }
        }
      },
      selectedExtraIndex() {
        const extra = this.trans[this.selectedTransIndex].extra.params;
        for (let i = 0; i < extra.length; i++) {
          if (extra[i].name === this.transExtraName) {
            return i
          }
        }
      },
    },
    methods: {
      ...mapActions("transport", {
        changeTransType: "changeTransType",
        changeTransExtraName: "changeTransExtraName",
        changeTransExtraHeader: "changeTransExtraHeader",
        changeTransExtraTitle: "changeTransExtraTitle"
      }),
      onTransTypeChange(value, index) {
        this.changeTransType(value);
        this.changeTransExtraHeader(this.trans[this.selectedTransIndex].extra.header);
        const firstExtra = this.trans[this.selectedTransIndex].extra.params[0].name;
        this.changeTransExtraName(firstExtra);
      }
    }
  }
</script>

<style lang="less">
  .transport {
    &--block {
      margin-right: 50px;
    }
    &--el {
      display: inline-flex;
      @media only screen and (max-width: 320px) {
        display: flex;
        width: 100%;
        justify-content: center;
      }
    }
  }

  .form {
    &--label {
      &__transport {
        padding: 10px 0;
        @media only screen and (max-width: 1195px) {
          height: 111px;
        }
        @media only screen and (max-width: 477px) {
          height: 123px;
        }
        @media only screen and (max-width: 477px) {
          height: 123px;
        }
        @media only screen and (max-width: 400px) {
          height: 113px;
        }
        @media only screen and (max-width: 374px) {
          height: 109px;
        }
        @media only screen and (max-width: 320px) {
          height: 100px;
        }
      }
      &__extra {
        align-items: center;
        @media only screen and (max-width: 576px) {
          max-width: none;
        }
      }
    }
    &--icon {
      &__electric-car {
        transform: scale(-1, 1);
      }
      &__truck {
        transform: scale(-1, 1);
      }
      &__moto {
        transform: scale(-1, 1);
      }
    }
  }
</style>




