<template>
	<fieldset class="form--block">
		<legend class="sub-header">Расширить лимиты ОСАГО</legend>
		<div class="form--text">
			Размер страховых выплат по ОСАГО составляет <b class="form--text__bold"> 100 тыс. грн. </b><br>
			По желанию Вы можете увеличить этот лимит.
		</div>
		<div class="form--row">
			<input class="form--radio"
				   @change="changeOsagoValue($event.target.value)"
				   ref="0"
				   id="osago-lims-no"
				   type="radio"
				   name="osago-limits"
				   :value="0">
			<label class="form--label form--label__osago" for="osago-lims-no">
				<div class="form--icon-wrap ">
					<svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 129 129">
						<use xlink:href="/Content/img/icons.svg#no-privilege"></use>
					</svg>
					<p class="label--text">Не нужно</p>
				</div>
			</label>

			<input class="form--radio"
				   @change="changeOsagoValue($event.target.value)"
				   ref="100000"
				   id="osago-lims-100k"
				   type="radio"
				   name="osago-limits"
				   :value="100000">
			<label class="form--label form--label__osago" for="osago-lims-100k">
				<div class="form--icon-wrap">
					<svg class="form--icon form--icon__100k" aria-hidden="true" viewBox="0 0 495 344">
						<use xlink:href="/Content/img/icons.svg#limits-1"></use>
					</svg>
					<p class="label--text">+100 000 грн</p>
				</div>
			</label>

			<input class="form--radio"
				   @change="changeOsagoValue($event.target.value)"
				   ref="150000"
				   id="osago-lims-150k"
				   type="radio"
				   name="osago-limits"
				   :value="150000">
			<label class="form--label form--label__osago" for="osago-lims-150k">
				<div class="form--icon-wrap">
					<svg class="form--icon form--icon__150k" aria-hidden="true" viewBox="0 0 675 425">
						<use xlink:href="/Content/img/icons.svg#limits-2"></use>
					</svg>
					<p class="label--text">+ 150 000 грн</p>
				</div>
			</label>

			<input class="form--radio"
				   @change="changeOsagoValue($event.target.value)"
				   ref="200000"
				   id="osago-lims-200k"
				   type="radio"
				   name="osago-limits"
				   :value="200000">
			<label class="form--label form--label__osago" for="osago-lims-200k">
				<div class="form--icon-wrap">
					<svg class="form--icon form--icon__200k" aria-hidden="true" viewBox="0 0 860 505">
						<use xlink:href="/Content/img/icons.svg#limits-3"></use>
					</svg>
					<p class="label--text">+ 200 000 грн</p>
				</div>
			</label>

			<input class="form--radio"
				   @change="changeOsagoValue($event.target.value)"
				   ref="500000"
				   id="osago-lims-500k"
				   type="radio"
				   name="osago-limits"
				   :value="500000">
			<label class="form--label form--label__osago" for="osago-lims-500k">
				<div class="form--icon-wrap">
					<svg class="form--icon form--icon__500k" aria-hidden="true" viewBox="0 0 1040 595">
						<use xlink:href="/Content/img/icons.svg#limits-4"></use>
					</svg>
					<p class="label--text">+ 500 000 грн</p>
				</div>
			</label>

			<input class="form--radio"
				   @change="changeOsagoValue($event.target.value)"
				   ref="1000000"
				   id="osago-lims-1m"
				   type="radio"
				   name="osago-limits"
				   :value="1000000">
			<label class="form--label form--label__osago" for="osago-lims-1m">
				<div class="form--icon-wrap">
					<svg class="form--icon form--icon__1m" aria-hidden="true" viewBox="0 0 1220 680">
						<use xlink:href="/Content/img/icons.svg#limits-5"></use>
					</svg>
					<p class="label--text">+ 1 000 000 грн</p>
				</div>
			</label>
		</div>
	</fieldset>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		computed: {
			...mapGetters("osago",{
				osago: "osago"
			})
		},
		methods: {
			...mapActions("osago", {
				changeOsagoValue: "changeOsagoValue"
			})
		},
		mounted () {
			for (let key in this.$refs) {
				if(this.$refs[key].value === this.osago.toString()) {
					this.$refs[key].checked = true;
				}
			}
		}
	}
</script>

<style lang="less">
.form{
	&--label {
		&__osago {
			@media only screen and (max-width:1195px) {
				height: 115px;
			}
		}
	}
}
</style>



