<template>
	<fieldset class="form--block">
		<legend class="sub2-header">Подлежит ли ТЗ ОТК?</legend>
		<p class="form--text"> ОТК - обязательный технический контроль, который включает в себя комплексную проверку технического состояния транспортного средства на предмет безопасности его для дорожного движения и охраны окружающей среды. <a href="#" class="form--link otk--link">Узнать подлежит ли мое ТЗ ОТК</a></p>
		<div class="form--row">
			<input @change="changeOtkValue(false)"
			:checked="!otk"
			class="form--radio"
			id="not-otk"
			type="radio"
			name="otk">
			<label class="form--label " for="not-otk">
				<div class="form--icon-wrap">
					<svg class="form--icon form--icon__not" aria-hidden="true" viewBox="0 0 480.204 480.204">
						<use xlink:href="/Content/img/icons.svg#not-otk"></use>
					</svg>
					<p class="label--text">Нет</p>
				</div>
			</label>

			<input @change="changeOtkValue(true)"
			:checked="otk"
			class="form--radio"
			id="otk"
			type="radio"
			name="otk">
			<label class="form--label " for="otk">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 480.204 480.204">
						<use xlink:href="/Content/img/icons.svg#otk"></use>
					</svg>
					<p class="label--text">Да</p>
				</div>
			</label>
		</div>

		<calendar></calendar>
	</fieldset>
</template>

<script>
	import Calendar from "./Calendar/Calendar.vue";
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
			}
		},
		computed: {
			...mapGetters("otk",{
				otk: "otk"
			})
		},
		methods: {
			...mapActions("otk", {
				changeOtkValue: "changeOtkValue"
			})
		},
		destroyed() {
			this.changeOtkValue(false);
		},
		components: {
			Calendar
		}
	}

</script>

<style lang="less">
.otk {
	&--link {
		display: block;
	}
}
</style>



